//> React
// Contains all the functionality necessary to define React components
import React from "react";
// Router
import { Link, Redirect } from "react-router-dom";

//> Redux
// Connect
import { connect } from "react-redux";
// Actions
import { getZombies, markDoneZombie } from "../../../store/actions/authActions";

//> Additional modules
// Copy to clipboard
import copy from "copy-to-clipboard";

//> MDB
// "Material Design for Bootstrap" is a great UI design framework
import {
  MDBContainer,
  MDBDataTable,
  MDBRow,
  MDBCol,
  MDBPopover,
  MDBPopoverHeader,
  MDBPopoverBody,
  MDBModal,
  MDBModalHeader,
  MDBModalBody,
  MDBModalFooter,
  MDBBtn,
  MDBBadge,
  MDBIcon,
  MDBProgress,
  MDBFormInline,
  MDBInput,
  MDBAlert,
  MDBSpinner,
  MDBCard,
  MDBCardBody,
} from "mdbreact";

//> Components
import { ResultChart } from "../../molecules/charts";

//> CSS
import "./zombie.scss";

class ZombieList extends React.Component {
  state = {
    data: {
      columns: [
        {
          label: "Chart",
          field: "chart",
          sort: "disabled",
        },
        {
          label: "Company",
          field: "company",
          sort: "disabled",
        },
        {
          label: "City",
          field: "city",
          sort: "disabled",
        },
        {
          label: "Contact",
          field: "contact",
          sort: "disabled",
        },
        {
          label: "Nutzerdaten",
          field: "access",
          sort: "disabled",
        },
      ],
      rows: undefined,
    },
  };

  componentWillReceiveProps(nextProps) {
    // Check if zombies have changed
    if (
      JSON.stringify(this.props.users) !== JSON.stringify(nextProps.zombies)
    ) {
      nextProps.zombies &&
        this.setState({ sync: false }, () => this.fillTable(nextProps.zombies));
    } else {
      this.setState({ sync: false });
    }
  }

  toggle = (zombie) => {
    if (!this.state.modal) {
      this.setState({
        modal: true,
        modalCat: {
          ...zombie,
        },
      });
    } else {
      this.setState({
        modal: false,
        modalUser: undefined,
      });
    }
  };

  getUserList = (zombies) => {
    return (
      zombies &&
      zombies.map((zombie, i) => {
        if (!zombie.disabled) {
          return {
            chart: (
              <div>
                <ResultChart
                  data={zombie.analysis[zombie.analysis.length - 1].results}
                  hideLabels
                />
              </div>
            ),
            company: (
              <>
                <p className="mb-0">{zombie.company_name}</p>
                <div className="mb-2">
                  {zombie.processed && (
                    <MDBBadge color="green">
                      <MDBIcon icon="check-circle" className="mr-1" />
                      Done
                    </MDBBadge>
                  )}
                </div>
                <MDBBtn
                  color="indigo"
                  className="px-3 m-0 mr-2"
                  size="sm"
                  onClick={() => this.toggle(zombie)}
                >
                  <MDBIcon icon="signature" />
                  Analysis
                </MDBBtn>
                {!zombie.processed && (
                  <MDBBtn
                    color="green"
                    className="px-3 m-0 mr-2"
                    size="sm"
                    onClick={() => this.markDone(zombie.uid)}
                  >
                    <MDBIcon icon="check" />
                    Done
                  </MDBBtn>
                )}
              </>
            ),
            city: zombie.city,
            contact: (
              <>
                <p
                  className="mb-1 clickable"
                  onClick={() => copy(zombie.email)}
                >
                  {zombie.email} <MDBIcon far icon="copy" />
                </p>
                {zombie.phone ? <p className="mb-1">{zombie.phone}</p> : null}
              </>
            ),
            access: (
              <>
                <p className="mb-1">E-Mail: {zombie.email}</p>
                <p
                  className="mb-0 clickable"
                  onClick={() => copy(zombie.password)}
                >
                  Password: {zombie.password} <MDBIcon far icon="copy" />
                </p>
              </>
            ),
          };
        }
      })
    );
  };

  fillTable = (zombies) => {
    this.setState({
      data: {
        ...this.state.data,
        rows: this.getUserList(zombies),
      },
    });
  };

  markDone = (uid) => {
    this.props.markDoneZombie(uid);
  };

  render() {
    const { auth, profile, zombies } = this.props;

    if (!profile.isLoaded) {
      return (
        <MDBContainer className="flex-center my-5 py-5">
          <MDBSpinner />
        </MDBContainer>
      );
    } else {
      // Check if logged in
      if (auth.uid === undefined) return <Redirect to="/" />;
      if (profile && !profile.coach) return <Redirect to="/" />;

      // Get firebase zombies
      if (!this.props.users) {
        this.props.getZombies();
      } else {
        if (!this.state.data.rows) {
          this.fillTable(this.props.users);
        }
      }

      return (
        <>
          <div id="zombielist">
            <>
              <MDBCard className="w-100">
                <MDBCardBody>
                  <h2 className="d-flex">
                    <MDBBadge color="indigo" className="mr-3">
                      Phase 2
                    </MDBBadge>{" "}
                    Zombies
                  </h2>
                  <p className="lead">
                    Phase 2 tries to transform <code>cats</code> into{" "}
                    <code>interested</code>.
                  </p>
                  <div className="text-right mb-4">
                    {this.state.removeCat && (
                      <>
                        <MDBBtn
                          className="px-3 m-0"
                          color="danger"
                          onClick={() => {
                            this.props.removeCat(this.state.removeCat.uid);
                            this.setState({ removeCat: undefined });
                          }}
                        >
                          <MDBIcon icon="check" />
                          Remove {this.state.removeCat.name}
                        </MDBBtn>
                        <MDBBtn
                          className="px-3 ml-2"
                          color="elegant"
                          outline
                          onClick={() =>
                            this.setState({ removeCat: undefined })
                          }
                        >
                          Cancel
                        </MDBBtn>
                      </>
                    )}
                  </div>
                  <MDBDataTable
                    striped
                    bordered
                    searching={false}
                    paging={false}
                    exportToCSV
                    entriesOptions={[10, 20, 50, 100]}
                    data={this.state.data}
                  />
                </MDBCardBody>
              </MDBCard>
            </>
          </div>
          {this.state.modal && this.state.modalCat && (
            <MDBModal
              modalStyle="primary"
              className="text-white modal-zombie"
              size="md"
              backdrop={true}
              isOpen={this.state.modal}
              toggle={this.toggle}
            >
              <MDBModalHeader
                className="text-center"
                titleClass="w-100"
                tag="p"
              >
                {this.state.modalCat.company_name} Analysis Details
              </MDBModalHeader>
              <MDBModalBody className="text-center">
                {this.state.modalCat && (
                  <ResultChart
                    data={
                      this.state.modalCat.analysis[
                        this.state.modalCat.analysis.length - 1
                      ].results
                    }
                  />
                )}
                {this.state.modalCat.analysis && (
                  <div className="text-left">
                    {Object.keys(
                      this.state.modalCat.analysis[
                        this.state.modalCat.analysis.length - 1
                      ].results
                    ).map((key, i) => {
                      if (
                        this.state.modalCat.analysis[
                          this.state.modalCat.analysis.length - 1
                        ].results[key].value < 70
                      ) {
                        return (
                          <>
                            <span>
                              {
                                this.state.modalCat.analysis[
                                  this.state.modalCat.analysis.length - 1
                                ].results[key].name
                              }
                            </span>
                            <MDBProgress
                              value={
                                this.state.modalCat.analysis[
                                  this.state.modalCat.analysis.length - 1
                                ].results[key].value
                              }
                              className="mt-1 mb-2"
                            />
                          </>
                        );
                      } else {
                        return null;
                      }
                    })}
                  </div>
                )}
              </MDBModalBody>
              <MDBModalFooter className="justify-content-center">
                <MDBBtn color="elegant" outline onClick={this.toggle}>
                  Close
                </MDBBtn>
              </MDBModalFooter>
            </MDBModal>
          )}
        </>
      );
    }
  }
}

const mapStateToProps = (state) => {
  return {
    auth: state.firebase.auth,
    profile: state.firebase.profile,
    users: state.auth.users,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getZombies: () => dispatch(getZombies()),
    markDoneZombie: (uid) => dispatch(markDoneZombie(uid)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZombieList);

